<template>
  <header>
    <div class="btn">
      <button @click="goback" type="button">
        <i class="el-icon-arrow-left"></i>
      </button>
      <button @click="goahead" type="button">
        <i class="el-icon-arrow-right"></i>
      </button>
    </div>
    <el-menu
      router
      mode="horizontal"
      text-color="#000000"
      active-text-color="#409EFF"
    >
      <el-menu-item index="/">首页</el-menu-item>
      <el-menu-item index="/toplists">排行榜</el-menu-item>
      <el-menu-item index="/artists">歌手</el-menu-item>
      <el-menu-item index="/playlists">分类歌单</el-menu-item>
      <el-menu-item index="/albums">数字专辑</el-menu-item>
    </el-menu>
    <el-input placeholder="搜索音乐/歌手/歌单" v-model="keyword" clearable>
      <el-button slot="append" @click="search"
        ><i class="el-icon-search"></i></el-button
    ></el-input>
  </header>
</template>

<script>
export default {
  data() {
    return {
      keyword: "",
    };
  },
  methods: {
    search() {
      if (this.keyword) {
        this.$router.push(`/search/${this.keyword}`);
      }
    },
    goback() {
      this.$router.back();
    },
    goahead() {
      this.$router.forward();
    },
  },
};
</script>

<style lang="less" scoped>
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
  border-bottom: 1px solid #dcdfe6;
  .btn {
    button {
      margin: 0 10px;
      border: none;
      outline: none;
      background-color: transparent;
      cursor: pointer;
      i {
        font-size: 20px;
        font-weight: bold;
      }
    }
  }
  .el-menu {
    .el-menu-item {
      font-size: 16px;
    }
  }
  .el-input {
    width: 250px;
  }
}
</style>