<template>
  <div class="player" v-if="$store.state.songInfo.id">
    <el-progress :percentage="percentage" :show-text="false"></el-progress>
    <div class="main">
      <div class="info">
        <el-image :src="$store.state.songInfo.al.picUrl"></el-image>
        <div class="text">
          <span>{{ $store.state.songInfo.name }}</span>
          <span> - </span>
          <router-link
            v-for="(item, index) in $store.state.songInfo.ar"
            :key="index"
            :to="`/artist/${item.id}`"
            >{{ item.name }}
          </router-link>
        </div>
      </div>
      <div class="control">
        <button type="button">
          <i class="el-icon-d-arrow-left" @click="previous"></i>
        </button>
        <button type="button" @click="handlePlay">
          <i v-if="playing" class="el-icon-video-pause"></i>
          <i v-else class="el-icon-video-play"></i>
        </button>
        <button type="button" @click="next">
          <i class="el-icon-d-arrow-right"></i>
        </button>
      </div>
      <div class="other">
        <span>{{ ct | timeFormat }} / {{ dt | timeFormat }}</span>
        <button type="button" @click="drawerVisible = true">
          <i class="el-icon-s-operation"></i>
        </button>
      </div>
    </div>
    <audio
      :src="`https://music.163.com/song/media/outer/url?id=${$store.state.songInfo.id}.mp3`"
      autoplay
      ref="audio"
    ></audio>
    <el-drawer title="播放列表" :visible.sync="drawerVisible" size="25%">
      <PlayInfoList></PlayInfoList>
    </el-drawer>
  </div>
</template>

<script>
import PlayInfoList from "@/components/PlayInfoList.vue";
export default {
  components: {
    PlayInfoList,
  },
  mounted() {
    setInterval(() => {
      if (this.playing) {
        this.ct = this.$refs.audio.currentTime * 1000;
        this.dt = this.$refs.audio.duration * 1000;
        this.percentage = (this.ct / this.dt) * 100;
      }
    }, 1000);
  },
  data() {
    return {
      playing: false,
      drawerVisible: false,
      ct: 0,
      dt: 0,
      percentage: 0,
    };
  },
  watch: {
    "$store.state.songInfo": {
      handler() {
        this.playing = true;
        this.percentage = 0;
      },
    },
  },
  methods: {
    getRowKey(row) {
      return row.id;
    },
    handlePlay() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
      this.playing = !this.$refs.audio.paused;
    },
    previous() {
      const index = this.$store.state.playlist.findIndex((item) => {
        return item.id === this.$store.state.songInfo.id;
      });
      if (index > 0) {
        // console.log('pre');
        this.$store.state.songInfo = this.$store.state.playlist[index - 1];
      }
    },
    next() {
      const index = this.$store.state.playlist.findIndex((item) => {
        return item.id === this.$store.state.songInfo.id;
      });
      if (index < this.$store.state.playlist.length - 1) {
        // console.log('next');
        this.$store.state.songInfo = this.$store.state.playlist[index + 1];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.player {
  .main {
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
    .info {
      display: flex;
      align-items: center;
      .el-image {
        width: 60px;
        height: 60px;
      }
      .text {
        margin-left: 15px;
        span {
          font-size: 15px;
          font-weight: bold;
        }
        a {
          font-size: 15px;
          color: #606266;
          // font-weight: bold;
        }
      }
    }
    .control {
      display: flex;
      button {
        margin-right: 30px;
        border: none;
        outline: none;
        background-color: transparent;
        cursor: pointer;
        i {
          font-size: 30px;
        }
      }
    }
    .other {
      display: flex;
      align-items: center;
      span{
        color: #606266;
        margin-right: 20px;
      }
      button {
        border: none;
        outline: none;
        background-color: transparent;
        cursor: pointer;
        i {
          font-size: 30px;
        }
      }
    }
  }
}
</style>