<template>
  <div>
    <ul>
      <li
        v-for="(item, index) in $store.state.playlist"
        :key="index"
        :class="{ active: item.id === $store.state.songInfo.id }"
      >
        <button @click="play(item)" type="button">
          <i class="el-icon-video-play"></i>
        </button>
        <div class="info">
          <p>{{ item.name }}</p>
          <div class="desc">
            <span>
              <span v-for="(subitem, index) in item.ar" :key="index"
                >{{ subitem.name }}
              </span>
            </span>
            <span>{{ item.dt | timeFormat }}</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  methods: {
    play(data) {
      this.$store.commit("setSongInfo", data);
    },
  },
};
</script>

<style lang="less" scoped>
div {
  li {
    padding: 15px 20px;
    display: flex;
    &.active {
      background-color: #ebeef5;
    }
    &:hover {
      background-color: #f2f6fc;
      button {
        display: block;
      }
    }
    button {
      display: none;
      margin-right: 20px;
      border: none;
      outline: none;
      background-color: transparent;
      cursor: pointer;
      &:hover {
        color: #409eff;
      }
      i {
        font-size: 25px;
      }
    }
    .info {
      flex: 1;
      p {
        font-size: 15px;
        font-weight: bold;
        margin-bottom: 10px;
      }
      .desc {
        display: flex;
        justify-content: space-between;
        span {
          font-size: 14px;
          color: #606266;
        }
      }
    }
  }
}
</style>